import React from 'react';
import {SidebarContainer, SidebarMenu, Icon, CloseIcon, SidebarLink, SideBtnWrap, SodebarRoute} from './sideberElements';

const Sidebar = ({isOpen, toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon/>
            </Icon>
            <SidebarMenu>
                <SidebarLink to='/'>Pizzas</SidebarLink>
                <SidebarLink to='/'>Desserts</SidebarLink>
                <SidebarLink to='/'>Full Menu</SidebarLink>
            </SidebarMenu>
            <SideBtnWrap>
                <SodebarRoute to='/'>Order Now</SodebarRoute>
            </SideBtnWrap>
        </SidebarContainer> 
    )
}

export default Sidebar;
